@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
html, body{
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}   
.container {
  height: 100vh;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: #251237;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.created-by a {
  color: #fff;
}
.keyboard-wrapper {
  height: 310px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.keyboard-wrapper[data-theme="light"] {
  background-color: #fff;
}

.keyboard-wrapper .row {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
}

.keyboard-wrapper .letter {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  background-color: #312344;
  margin: 5px;
  width: 60px;
  height: 60px;
  border-radius: 8px;
}

.keyboard-wrapper .letter span {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}

.keyboard-wrapper .space-bar {
  width: 500px;
}

.keyboard-wrapper .active-letter {
  background-color: #f9ca24;
  color: white;
}

.accuracy-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
}


.accuracy-wrapper span{
  font-size: 2em;
  color: #251237;
  text-align: center;
}

.accuracy-wrapper #number{
  font-size: 7em;
}

.accuracy-wrapper #number span{
  font-size: 30px;
}

.accuracy-wrapper #text{
  letter-spacing: 2px;

}


.text-wrapper {
  padding: 10px;
  border-bottom: 1px solid #392a47;
}

.text-wrapper .character {
  color: #ffffff;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: 2px;
}

.text-wrapper .visited {
  color: #6ab04c;
}

.text-wrapper .typo {
  color: #d02d2a;
}

.text-wrapper .space {
  width: 10px;
}

.wordpermin-wrapper {
  border-top: 1px solid #251237;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  width: 100%;
}

.wordpermin-wrapper span{
  font-size: 2em;
  color: #251237;
  text-align: center;
}

.wordpermin-wrapper #number{
  font-size: 7em;
}

.wordpermin-wrapper #text{
  letter-spacing: 2px;

}

.sound-switch {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.sound-switch .btn-label{
  margin-right: auto;
  color: #fff;
  font-size: 1.3em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.sound-switch .btn-label span{
  margin: 2px;
}


.sound-switch .sound-btn{
  border: 0;
  font-size: 1.2em;
  font-weight: bold;
  cursor: pointer;
  padding: 8px 10px;
  color: #251237;
  background: #412f5a;
}

.sound-switch button:focus{
  outline: 0;
}

.sound-switch button[for="on"]{
  border-radius: 8px  0  0 8px;
}

.sound-switch button[for="off"]{
  border-radius: 0  8px  8px 0;
}

.sound-switch .active-btn{
  background-color: #22a6b3;

}
.main-content {
  border: 1px solid #392a47;
  height: 640px;
  width: 1300px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
}

.main-content .left {
  border: 1px solid #392a47;
  width: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 18px;
  
}

.main-content .left .left-background{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 18px;
  border-radius: 8px;
  background-color: #fff;
}

.main-content .center {
  border: 1px solid #392a47;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.main-content .right {
  border: 1px solid #392a47;
  width: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 18px;
}

